/* RIPE App Design System Color custom properties */

:root {
  --ripe-app-accent: #25316a;
  --ripe-app-darkbase: hsl(248, 100%, 10%, 1);

  --ripe-app-backgrounds: var(--ripe-app-primary-100);
  --ripe-app-info: #29abe2;
  --ripe-app-positive: #6ebe49;
  --ripe-app-worried: #ff6a00;
  --ripe-app-alarmed: #ed1c24;
  --ripe-app-accent: var(--ripe-app-primary-80);

  --ripe-app-primary-100: #080037;
  /* was #131f48, which is the base color for the
    rest of this range.
    Using #080037 as a basis for the range, will yield
    way too purplr colors (seel below).
    */
  --ripe-app-primary-80: #1b204c;
  --ripe-app-primary-60: #2f376b;
  --ripe-app-primary-40: #9ea5c2;
  --ripe-app-primary-20: #d7dce8;
  --ripe-app-primary-0: #eceff4;

  /* THIS IS PURPLE ! */
  /* --ripe-app-primary-100: #080037;
    --ripe-app-primary-80: #39335f;
    --ripe-app-primary-60: #6b6687;
    --ripe-app-primary-40: #9c99af;
    --ripe-app-primary-20: #ceccd7;
    --ripe-app-primary-0: #e6e5eb; */

  --ripe-app-secondary-100: #009ba6;
  --ripe-app-secondary-80: #00b0ba;
  --ripe-app-secondary-60: #3dbdc6;
  --ripe-app-secondary-40: #7bd1db;
  --ripe-app-secondary-20: #c2e8ed;
  --ripe-app-secondary-0: #e1eff4;

  --ripe-app-positive-100: #6ebe49;
  --ripe-app-positive-80: #7cce5d;
  --ripe-app-positive-60: #9bd78b;
  --ripe-app-positive-40: #c4e4bc;
  --ripe-app-positive-20: #ddeddc;
  --ripe-app-positive-0: #edf3ed;

  --ripe-app-worried-100: #ff6a00;
  --ripe-app-worried-80: #ff7a36;
  --ripe-app-worried-60: #ff9660;
  --ripe-app-worried-40: #ffb491;
  --ripe-app-worried-20: #ffdacc;
  --ripe-app-worried-0: #ffeae7;

  --ripe-app-navbar-background: var(--ripe-app-primary-100);
  --ripe-app-navbar-menuitem: var(--ripe-app-primary-20);
  --ripe-app-navbar-active-menuitem: #ffffff;
  --ripe-app-navbar-footer-item: #9d9aac;
  --ripe-app-navbar-indicator: #f59331;

  --ripe-app-navrail-tooltip-bg: #363738;
  --ripe-app-navrail-tooltip-text: #ffffff;
  --ripe-app-navrail-tooltip-border: #363738;

  --ripe-app-header-icon-bg: #c0c0c0;
  --ripe-app-header-icon-hover: var(--ripe-app-primary-100);

  --ripe-app-border-line: #2f2c56;

  --ripe-header-padding-top: 0px;
}
